:root {
  color-scheme: light;
}

/* Override Tailwind resets that conflict with our design system */
body {
  /* Let main.css handle body styles */
}

a {
  color: inherit;
  text-decoration: none;
}

/* Ensure workspace textareas use our design system */
.workspace textarea,
.input-panel textarea {
  font-family: var(--font-body);
  font-size: var(--fs-body);
  line-height: var(--lh-relaxed);
  padding: var(--space-md);
  color: var(--text-main);
  background: var(--bg-card);
  border: 1px solid var(--border-subtle);
  border-radius: var(--radius-input);
}

.workspace textarea::-moz-placeholder, .input-panel textarea::-moz-placeholder {
  color: var(--text-muted);
  opacity: 1;
}

.workspace textarea::placeholder,
.input-panel textarea::placeholder {
  color: var(--text-muted);
  opacity: 1;
}

/* CRITICAL: Score Header Layout Fix */
.workspace .score-header,
.report-panel .score-header {
  display: flex !important;
  flex-direction: row !important;
  align-items: flex-start;
  gap: var(--space-xl);
  padding: var(--space-xl);
  background: var(--bg-card);
  border: 1px solid var(--border-subtle);
  border-radius: var(--radius-card);
  margin-bottom: var(--space-xl);
}

/* Score Dial must be visible and fixed size */
.workspace .score-dial-container,
.report-panel .score-dial-container {
  display: flex !important;
  flex-direction: column;
  align-items: center;
  gap: var(--space-sm);
  flex-shrink: 0;
}

.workspace .score-dial,
.report-panel .score-dial {
  position: relative;
  width: 120px;
  height: 120px;
  flex-shrink: 0;
}

.workspace .score-dial svg,
.report-panel .score-dial svg {
  width: 100%;
  height: 100%;
  transform: rotate(-90deg);
}

/* Ensure score content takes remaining space */
.workspace .score-content,
.report-panel .score-content {
  flex: 1;
  min-width: 0;
}

/* Report width stability - prevent tab content from changing widths */
.report-content {
  min-height: 400px;
}

.report-section.active {
  width: 100%;
}

/* Subscore Band Colors */
.subscore-band.band-exceptional {
  color: var(--score-exceptional);
}

.subscore-band.band-strong {
  color: var(--score-strong);
}

.subscore-band.band-good {
  color: var(--success);
}

.subscore-band.band-needs-work {
  color: var(--warning);
}

.subscore-band.band-risk {
  color: var(--error);
}

/* Section Review Cards */
.section-review-card {
  background: var(--bg-card);
  border: 1px solid var(--border-subtle);
  border-radius: var(--radius-card);
  padding: var(--space-lg);
  margin-bottom: var(--space-md);
}

.section-review-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: var(--space-md);
  padding-bottom: var(--space-sm);
  border-bottom: 1px solid var(--border-subtle);
}

.section-review-name {
  font-family: var(--font-display);
  font-size: var(--fs-lg);
  font-weight: 700;
  color: var(--text-main);
}

.section-review-grade {
  font-size: var(--fs-small);
  font-weight: 700;
  padding: var(--space-xxs) var(--space-sm);
  border-radius: var(--radius-sm);
  background: var(--accent-wash);
  color: var(--accent);
}

.section-review-grade.grade-a-minus,
.section-review-grade.grade-a {
  background: rgba(47, 127, 95, 0.12);
  color: var(--success);
}

.section-review-grade.grade-b-plus,
.section-review-grade.grade-b {
  background: var(--accent-wash);
  color: var(--accent);
}

.section-review-grade.grade-c-plus,
.section-review-grade.grade-c {
  background: rgba(217, 119, 6, 0.12);
  color: var(--warning);
}

.section-review-body {
  display: flex;
  flex-direction: column;
  gap: var(--space-sm);
}

.section-review-item {
  font-size: var(--fs-small);
  line-height: var(--lh-relaxed);
}

.section-review-label {
  font-weight: 600;
  margin-right: var(--space-xs);
}

.section-review-working .section-review-label {
  color: var(--success);
}

.section-review-missing .section-review-label {
  color: var(--warning);
}

.section-review-fix .section-review-label {
  color: var(--accent);
}

.section-review-text {
  color: var(--text-soft);
}

.section-review-priority {
  margin-top: var(--space-xs);
}

.priority-badge {
  font-size: var(--fs-xs);
  font-weight: 600;
  text-transform: capitalize;
}

.priority-badge.priority-high {
  color: var(--error);
}

.priority-badge.priority-medium {
  color: var(--warning);
}

.priority-badge.priority-low {
  color: var(--text-muted);
}

/* Fix Items */
.fix-item {
  display: flex;
  gap: var(--space-md);
  padding: var(--space-md);
  margin-bottom: var(--space-sm);
  background: var(--surface-tint);
  border-radius: var(--radius-md);
}

.fix-number {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 28px;
  height: 28px;
  flex-shrink: 0;
  background: var(--accent);
  color: white;
  border-radius: 50%;
  font-size: var(--fs-small);
  font-weight: 700;
}

.fix-content {
  flex: 1;
}

.fix-text {
  color: var(--text-main);
  font-size: var(--fs-body);
  line-height: var(--lh-relaxed);
  margin-bottom: var(--space-xs);
}

.fix-section-ref {
  display: inline-block;
  font-size: var(--fs-xs);
  font-weight: 500;
  color: var(--text-muted);
  background: var(--bg-card);
  padding: var(--space-xxs) var(--space-xs);
  border-radius: var(--radius-sm);
  margin-right: var(--space-xs);
}

.fix-impact {
  font-size: var(--fs-xs);
  font-weight: 600;
  text-transform: capitalize;
}

.fix-impact.fix-impact-high {
  color: var(--success);
}

.fix-impact.fix-impact-medium {
  color: var(--warning);
}

/* Rewrite Category Labels */
.rewrite-category-label {
  font-size: var(--fs-xs);
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: 0.05em;
  color: var(--accent);
  margin-bottom: var(--space-sm);
}

.rewrites-container {
  display: flex;
  flex-direction: column;
  gap: var(--space-lg);
}

/* Ideas Sections */
.ideas-list {
  display: flex;
  flex-direction: column;
  gap: var(--space-md);
}

.ideas-section-card {
  background: var(--surface-tint);
  border-radius: var(--radius-md);
  padding: var(--space-md);
}

.ideas-section-title {
  font-weight: 600;
  color: var(--text-main);
  margin-bottom: var(--space-xs);
}

.ideas-archetype {
  display: inline-block;
  font-size: var(--fs-xs);
  font-weight: 700;
  color: var(--accent);
  text-transform: uppercase;
  letter-spacing: 0.03em;
}

.ideas-why {
  font-size: var(--fs-small);
  color: var(--text-muted);
  margin-top: var(--space-xs);
  line-height: var(--lh-relaxed);
}

.ideas-how-to-use {
  margin-top: var(--space-lg);
  padding: var(--space-md);
  background: var(--accent-wash);
  border-radius: var(--radius-md);
  font-size: var(--fs-small);
  color: var(--text-soft);
  line-height: var(--lh-relaxed);
}

/* Role Fit Grid */
.role-fit-grid {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: var(--space-lg);
  margin-bottom: var(--space-lg);
}

.role-fit-col {
  padding: var(--space-md);
  background: var(--surface-tint);
  border-radius: var(--radius-md);
}

.role-fit-title {
  font-size: var(--fs-small);
  font-weight: 700;
  margin-bottom: var(--space-sm);
}

.role-fit-title.best-fit {
  color: var(--success);
}

.role-fit-title.stretch {
  color: var(--warning);
}

.role-fit-list {
  list-style: disc;
  padding-left: var(--space-lg);
  font-size: var(--fs-small);
  color: var(--text-soft);
}

.role-fit-list li {
  margin-bottom: var(--space-xxs);
}

.role-fit-meta {
  display: flex;
  flex-wrap: wrap;
  gap: var(--space-lg);
  padding-top: var(--space-md);
  border-top: 1px solid var(--border-subtle);
}

.role-fit-meta-item {
  display: flex;
  flex-direction: column;
  gap: var(--space-xxs);
}

.role-fit-meta-label {
  font-size: var(--fs-xs);
  font-weight: 600;
  color: var(--text-muted);
  text-transform: uppercase;
  letter-spacing: 0.03em;
}

.role-fit-meta-value {
  font-size: var(--fs-small);
  color: var(--text-main);
}

/* Positioning Text */
.positioning-text {
  color: var(--text-soft);
  line-height: var(--lh-relaxed);
  font-size: var(--fs-body);
}
.hover\:border-blue-400:hover{
  --tw-border-opacity: 1;
  border-color: rgb(96 165 250 / var(--tw-border-opacity));
}
.hover\:bg-blue-50:hover{
  --tw-bg-opacity: 1;
  background-color: rgb(239 246 255 / var(--tw-bg-opacity));
}
.hover\:bg-blue-700:hover{
  --tw-bg-opacity: 1;
  background-color: rgb(29 78 216 / var(--tw-bg-opacity));
}
.hover\:bg-slate-200:hover{
  --tw-bg-opacity: 1;
  background-color: rgb(226 232 240 / var(--tw-bg-opacity));
}
.hover\:bg-slate-50:hover{
  --tw-bg-opacity: 1;
  background-color: rgb(248 250 252 / var(--tw-bg-opacity));
}
.focus\:border-blue-500:focus{
  --tw-border-opacity: 1;
  border-color: rgb(59 130 246 / var(--tw-border-opacity));
}
.focus\:bg-white:focus{
  --tw-bg-opacity: 1;
  background-color: rgb(255 255 255 / var(--tw-bg-opacity));
}
.focus\:outline-none:focus{
  outline: 2px solid transparent;
  outline-offset: 2px;
}
.focus\:ring-2:focus{
  --tw-ring-offset-shadow: var(--tw-ring-inset) 0 0 0 var(--tw-ring-offset-width) var(--tw-ring-offset-color);
  --tw-ring-shadow: var(--tw-ring-inset) 0 0 0 calc(2px + var(--tw-ring-offset-width)) var(--tw-ring-color);
  box-shadow: var(--tw-ring-offset-shadow), var(--tw-ring-shadow), var(--tw-shadow, 0 0 #0000);
}
.focus\:ring-blue-100:focus{
  --tw-ring-opacity: 1;
  --tw-ring-color: rgb(219 234 254 / var(--tw-ring-opacity));
}
.disabled\:cursor-not-allowed:disabled{
  cursor: not-allowed;
}
.disabled\:opacity-70:disabled{
  opacity: 0.7;
}
@media (min-width: 1024px){
  .lg\:grid-cols-2{
    grid-template-columns: repeat(2, minmax(0, 1fr));
  }
}
